html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-arrow-long {
  width: 14.49em;
  height: 1em;
  fill: currentColor;
}

.icon-arrow-right {
  width: 2.92em;
  height: 1em;
  fill: currentColor;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: currentColor;
}

body {
  background: #FFF;
  line-height: 1.4;
}

body a {
  text-decoration: none;
}

.inner {
  max-width: 1170px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px 0 115px;
}

.block {
  display: flex;
}

.header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100px;
  height: 100%;
  z-index: 100;
}

.header.is-active {
  width: 100%;
  height: 100px;
}

.header__wrap {
  position: absolute;
  top: 100px;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(26, 29, 36, 0.6);
  transition: background-color .3s;
  z-index: -1;
}

.header.is-active .header__wrap {
  top: 0;
  background-color: #1a1d24;
}

.header__contacts {
  height: 20px;
  text-align: center;
  position: absolute;
  transform-origin: center;
  transform: rotate(-90deg) translateY(-50%);
  display: flex;
  align-items: center;
  justify-content: center;
  top: 50%;
  left: 0;
  right: 0;
  bottom: 0;
}

.header.is-active .header__contacts {
  transform: translateY(-50%);
}

.header__contacts a {
  display: block;
  flex-shrink: 0;
}

.header__contacts a:not(:first-child) {
  margin-left: 30px;
}

.phone,
.email {
  font-size: 22px;
  color: #fff;
}

.burger {
  position: relative;
  width: 100%;
  height: 100px;
  background-color: rgba(9, 10, 15, 0.7);
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color .5s ease;
}

.no-touch .burger:hover .burger__line {
  width: 100%;
}

.header.is-active .burger {
  width: 100px;
  height: 100%;
}

.burger__icon {
  display: block;
  width: 27px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.burger__line {
  display: block;
  width: 100%;
  height: 2px;
  margin: 6px 0;
  background-color: #fff;
  transform-origin: center;
  transition: width .3s, transform .3s, margin .3s;
}

.burger.is-active .burger__line {
  width: 100%;
  margin: 0;
  height: 0;
}

.burger__line_md {
  width: 73%;
  margin: 0;
}

.burger.is-active .burger__line_md {
  width: 100%;
  transform: rotate(45deg);
  transition-delay: .3s;
  height: 2px;
}

.burger__line_sm {
  width: 42%;
  margin: 0;
}

.burger.is-active .burger__line_sm {
  width: 100%;
  transform: rotate(-45deg);
  transition-delay: .3s;
  height: 2px;
  margin-top: -2px;
}

.burger-menu {
  position: fixed;
  left: 100px;
  top: 0;
  bottom: 0;
  width: 320px;
  background-color: #1a1d24;
  border-left: 1px solid #2d2d2d;
  transition: transform .3s ease, opacity .3s ease;
  transform: translateX(-100%);
  opacity: 0;
}

.burger-menu.is-active {
  transform: translateX(0);
  opacity: 1;
}

.burger-menu__inner {
  position: relative;
  overflow-y: auto;
  width: 100%;
  height: 100%;
  padding: 30px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.burger-menu__item {
  position: relative;
  padding-left: 40px;
}

.burger-menu__item:not(:first-child) {
  margin-top: 20px;
}

.burger-menu__item.is-active:before {
  content: '';
  position: absolute;
  width: 20px;
  height: 2px;
  background-color: #0d9838;
  top: 0;
  bottom: 0;
  left: 0;
  margin: auto;
}

.burger-menu__link {
  color: #fff;
  font-size: 18px;
  transition: color .3s;
}

.no-touch .burger-menu__link:hover {
  color: #0d9838;
}

.hero {
  min-height: 100vh;
  position: relative;
  overflow: hidden;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  display: flex;
}

.hero__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  height: 100%;
  color: #fff;
}

.hero__left {
  flex: 0 0 58.33333%;
  max-width: 58.33333%;
}

.hero__right {
  flex: 0 0 33.33333%;
  max-width: 33.33333%;
  margin-left: 20px;
  height: 100%;
}

.hero-menu {
  height: 100%;
}

.hero-menu__item {
  position: relative;
  height: 33.33333%;
  display: flex;
  align-items: center;
  transition: height .3s;
  border: 1px solid #2d2d2d;
}

.hero-menu__item:first-child, .hero-menu__item:last-child {
  border-top: none;
  border-bottom: none;
}

.hero-menu__wrap {
  padding: 20px 40px;
  width: 100%;
  display: flex;
  align-items: center;
}
